.App {
  margin: 0;
}

/***********************************HOME PAGE******************************************/

#banner {
  width: 100%;
  height: 350px;
  background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
    url(/static/media/boat.576bd9e9.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

#banner h1 {
  text-align: center;
  font-size: 2.3rem;
  font-family: "Times New Roman", Times, serif;
  color: white;
  padding-top: 40px;
  letter-spacing: 2px;
}

#banner p {
  text-align: center;
  font-size: 0.9rem;
  color: white;
  padding: 20px;
  letter-spacing: 2px;
}

#description-section {
  height: 300px;
  background-color: #f4f4f2;
  color: #495464;
  text-align: center;
}

#description-section h4 {
  padding: 30px 15px;
  font-size: 1.6rem;
  font-family: "Times New Roman", Times, serif;
}

#description-section p {
  color: grey;
  font-size: 0.9rem;
  padding: 0px 50px;
  margin-bottom: 40px;
}

#description-section button {
  height: 40px;
  width: 180px;
  border-radius: 3px;
  border-color: #495464;
  color: #495464;
  background-color: #e8e8e8;
}

h2.category-info {
  text-align: center;
  padding: 20px;
  letter-spacing: 2px;
  color: #495464;
  font-size: 1.5rem;
}

p.category-info {
  text-align: center;
  letter-spacing: 2px;
}

/********************************************Instrucions Page********************************************/

#instructions-banner {
  height: 250px;
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
    url(/static/media/instructions-img.c519ee6f.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: white;
}

#instructions-banner h1 {
  font-size: 1.4rem;
  text-align: center;
  padding: 170px 15px;
  letter-spacing: 2px;
  line-height: 30px;
}

#instructions-container {
  margin: 20px;
}

#instructions-container h3 {
  text-align: center;
  color: #495464;
  letter-spacing: 1px;
  margin-bottom: 25px;
}

#instructions-container li {
  margin: 10px 5px;
}

#instructions-container ul {
  margin-bottom: 30px;
}

/********************************************ProfilePage********************************************/

#profile-image {
  width: 130px;
  height: 130px;
  object-fit: cover;
  border-radius: 100%;
}



/********************************************DetailsPage********************************************/

.participant-img {
  width: 35px;
  height: 35px;
  object-fit: cover;
  border-radius: 100%;
  padding: 2px;
}

/********************************************Signup********************************************/

#signup-page {
  text-align: center;
  background: url(/static/media/signup-img.a66eda50.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 600px;
  padding-top: 40px;
}

#signup-page > form {
  border: 1px solid white;
  height: 450px;
  width: 80%;
  margin: 0 auto;
  background-color: white;
  opacity: 0.8;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  border-radius: 3px;
  margin-bottom: 20px;
  padding-bottom: 20px;
}

#uploaded-img {
  border-radius: 50%;
  height: 100px;
  width: 100px;
  object-fit: cover;
}

#signup-page h1 {
  color: #495464;
  font-size: 30px;
  letter-spacing: 2px;
}

#signup-page p {
  color: white;
}

#signup-page input {
  width: 200px;
  margin: 10px;
  height: 35px;
}

#signup-button {
  background-color: #495464;
  border: none;
  color: white;
  letter-spacing: 2px;
}

/********************************************Login********************************************/

#login-page {
  text-align: center;
  background-image: url(/static/media/login-img.824242f4.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 450px;
  padding-top: 40px;
}

#login-page > form {
  border: 1px solid white;
  height: 350px;
  width: 75%;
  margin: 0 auto;
  background-color: white;
  opacity: 0.8;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  border-radius: 3px;
  margin-bottom: 20px;
  margin-top: 15px;
  padding-bottom: 20px;
}

#login-page input {
  width: 200px;
  height: 35px;
}

#login-page h1 {
  margin-top: 20px;
  color: #495464;
  font-size: 30px;
  letter-spacing: 2px;
}

#login-btn {
  background-color: #495464;
  color: white;
  letter-spacing: 2px;
  border: none;
}

.navbar {
  display: inline-block;
  width: 100% !important;
  margin: 0 !important;
  background-color: #495464 !important;
}

#sun-btn {
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  border-color: #495464;
  border: none;
  outline: none;
}

#moon-btn {
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  border-color: #495464;
  outline: none;
  border: none;
  border-left: 1px solid;
}

.navbar-brand {
  color: #f4f4f2 !important;
  letter-spacing: 2px;
}

.navbar-toggler {
  outline: none;
}

.container-fluid {
  width: 100% !important;
  margin: 0 !important;
  display: flex;
  justify-content: space-between;
}

.nav-left img {
  border-radius: 50%;
  width: 40px;
  height: 40px;
  object-fit: cover;
}

#footer-div {
  text-align: center;
  background-color: #495464;
  color: #f4f4f2;
  letter-spacing: 3px;
  height: 120px;
}

#disclaimer {
  font-size: 5.1rem;
}

#footer-div p {
  font-size: 0.7rem;
  padding-top: 10px;
}

#footer-div i {
  font-size: 0.8rem;
  padding: 8px
}

.categoryCard {
  height: 250px;
  width: 80%;
  margin: 30px auto;
  /* border: 1px solid #495464; */
  border-radius: 10px;
  text-align: center;
  color: white;
  box-shadow: 4px 4px 5px 1px rgba(73,84,100,0.86);
  -webkit-box-shadow: 4px 4px 5px 1px rgba(73,84,100,0.86);
  -moz-box-shadow: 4px 4px 5px 1px rgba(73,84,100,0.86);
}

.categoryCard:hover {
  box-shadow: none;
}

.categoryCard h3 {
  font-size: 1.8rem;
  letter-spacing: 4px;
  padding-top: 190px;
}

#sports {
  background-image: url(/static/media/sports.a15ec4f9.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

#beauty {
  background-image: url(/static/media/beauty.387df454.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

#creativity {
  background-image: url(/static/media/creativity.58add34d.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

#food-drink {
  background-image: url(/static/media/cocktails.9b4227f8.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

#performing-arts {
  background-image: url(/static/media/arts.6e12732b.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

#performing-arts h3 {
  padding-top: 150px;
}

#languages {
  background-image: url(/static/media/languages.64269033.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

#other {
  background-image: url(/static/media/other.dc27ee0b.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.search-container {
  width: 210px;
  margin: 0 auto;
  padding-top: 7px;
}

.search-bar {
  opacity: 0.75;
  border: none;
  text-align: center;
  height: 40px;
  width: 210px;
  margin: 0 auto;
  outline: none;
  border-radius: 3px;
}

.workshop-card {
  width: 90%;
  height: 180px;
  border: 1px solid #e8e8e8;
  border-radius: 7px;
  margin: 20px auto;
  background-color: #e8e8e8;
  box-shadow: 4px 4px 5px 1px rgba(73, 84, 100, 0.5);
  -webkit-box-shadow: 4px 4px 5px 1px rgba(73, 84, 100, 0.5);
  -moz-box-shadow: 4px 4px 5px 1px rgba(73, 84, 100, 0.5);
}

.card-left {
  display: flex;
}

.icons {
  display: flex;
  justify-content: flex-end;
  padding-right: 10px;
}

.icons button {
  background-color: #bbbfca;
  border: none;
  color: #495464;
  height: 30px;
  width: 110px;
}

.workshop-card img {
  width: 100px;
  height: 100px;
  object-fit: cover;
}

.card-image {
  padding: 10px;
}

.workshop-info {
  display: flex;
}

.credits {
  color: #495464;
  font-weight: 600;
}

.card-center {
  display: flex;
  flex-direction: column;
  padding: 10px;
}

.workshop-info h3 {
  font-size: 20px;
  letter-spacing: 2px;
}

#edit-form {
    display: flex;
    flex-direction: column;
    width: 90%;
    margin: 0 auto;
    border: none;
    border-radius: 3px;
    background-color: #bbbfca;
}

#edit-form input {
    background-color: #f4f4f2;
    border: none;
    border-radius: 3px;
    width: 300px;
    margin: 20px auto;

}

#edit-form textarea {
    background-color: #f4f4f2;
    border: none;
    border-radius: 3px;
    width: 300px;
    margin: 20px auto;

}

#category-checkboxes {
    display: flex;
    flex-wrap: wrap;
}

.checkbox-pair {
    display: flex;
    border: 1px solid black;
    width: 100%;
    flex-direction: row;
    align-items: flex-start;
}


